// Modal
#app-dialog, #app-dialog2 {
    display: none;
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    background: rgba($color-black, .7);
    > div {
        background: $color-grey2;
        min-height: 30vh;
        width: 80vw;
        max-width: 800px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 20px;
        box-sizing: border-box;
        padding: 20px 10px 80px 10px;
    }
}
.modal__title {
    color: $color-red;
    text-align: center;
    font-size: 1.6rem;
    line-height: 3rem;
    margin: 0 0 20px;
}

//Ajout Seb.L
.modalHeader {
    color: $color-red;
    text-align: center;
    font-size: 1.6rem;
    line-height: 3rem;
    margin: 0 0 20px;
}
.modalContent {
    background: $color-white;
    width: 100%;
    height: 48px;
    border: 0;
    box-sizing: border-box;
    padding: 8px 20px;
    color: $color-blue;
    border-radius: 30px;
}
.modalFooter {
    bottom: 20px;
    display: flex;
    margin: 0;
    .btn {
        margin: 0 10px;
    }
    .btn--alert {
        background-color: $color-red;
        color: $color-white;
    }
}