// Maitrise des comp√©tences
.maitrise {
    margin: 0 10px;
    .spotlight,
    .innerTitle {
        margin: 0 0 20px;
    }
    // A verifier
    .materialField,
    .radioMaterial:not(:checked) + .labelRadioMaterial,
    .radioMaterial:checked + .labelRadioMaterial,
    .radioMaterial:not(:checked) + .labelRadioMaterial::before,
    .radioMaterial:not(:checked) + .labelRadioMaterial::after,
    .radioMaterial:checked + .labelRadioMaterial::before,
    .radioMaterial:checked + .labelRadioMaterial::after {
        width: 20px;
        height: 20px;
    }
}
.maitrise__form {
    margin: 65px 0 0;
}
.maitrise__container {
    min-width: 90px;
    max-width: 90px;
    display: flex;
    justify-content: space-between;
    margin: 0 0 0 10px;
}
.maitrise__titles {
    display: flex;
    margin: 0 0 15px;
    font-weight: bold;
    p {
        flex-grow: 1;
    }
    span {
        display: block;
        transform: rotate(-90deg);
        transform-origin: left;
        width: 20px;
        margin: 0 8px;
    }
}
.maitrise__list {
    height: 220px;
    overflow-y: scroll;
}
.maitrise__item {
    display: flex;
    align-items: center;
    margin: 0 0 10px;
    &:last-child {
        margin-bottom: 0;
    }
}
.maitrise__title {
    flex-grow: 1;
}