// Bienvenue
.welcome {
    .main {
        height: calc(100vh - 60px);
    }
    .underNav {
        display: none;
    }
}
.bienvenue {
    height: calc(100vh - 60px);
    box-sizing: border-box;
    padding: 15px 0 0;
    background: url("../images/bg-city.png") no-repeat center 80px;
    background-size: contain;
    position: relative;
    &:after {
        content: '';
        height: 90px;
        width: 100vw;
        position: absolute;
        bottom: 20px;
        left: 0;
        right: 0;
        background: url("../images/bg-interior.png") no-repeat center bottom;
        background-size: contain;
    }
}
.bienvenue__intro {
    text-align: center;
    p {
        margin: 0 0 10px;
    }
}
.bienvenue__list {
    margin: 75px 0 0;
    max-height: 430px;
    overflow-y: scroll;
}
.bienvenue__item {
    text-align: center;
    margin: 0 0 20px;
    &:last-child {
        margin-bottom: 0;
    }
    a {
        display: inline-block;
        width: 160px;
        height: 160px;
        background: rgb(0,137,171);
        background: linear-gradient(90deg, rgba(0,137,171,1) 0%, rgba(0,199,248,1) 100%);
        border-radius: 50%;
        text-decoration: none;
        color: $color-white;
        box-sizing: border-box;
        padding: 55px 10px 0 10px;
        box-shadow: 0 3px 6px rgba(0, 0, 0, .16);
        span {
            display: block;
            @extend %kanit;
        }
        .title {
            font-size: 1.6rem;
            font-weight: bold;
            margin: 0 0 10px;
        }
        .subtitle {
            font-size: 1.2rem;
        }
    }
}