// PIF
.pif {
    margin: 0 10px;
    .spotlight,
    .innerTitle {
        margin: 0 0 20px;
    }
    .innerTitle {
        margin: 0 0 40px;
    }
}
#circlePif {
    margin: 0 30px;
    #shape1 {
        fill: $color-blue;
    }
    #shape2 {
        fill: $color-green;
    }
    #shape3 {
        fill: $color-light-blue;
    }
    .svgText {
        fill: $color-white;
        font-size: 0.9rem;
    }
    .svgText--mid {
        text-anchor: middle;
    }
}