// Parcours
.parcours {
    margin: 0 20px;
    .spotlight,
    .innerTitle {
        margin: 0 0 20px;
    }
}
.parcours__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-height: 370px;
    overflow-y: scroll;
}
.parcours__item {
    border-radius: 50%;
    box-shadow: 0 3px 6px rgba(0,0,0,.16);
    margin: 10px;
    a,
    span {
        display: block;
        text-decoration: none;
        color: $color-white;
        width: 140px;
        height: 140px;
        text-align: center;
        font-size: 1.8rem;
        box-sizing: border-box;
        padding: 80px 0 0;
        border-radius: 50%;
    }
    span {
        background-color: #bce0fd;
    }
    &.isComplete {
        a {
            background-color: $color-blue;
        }
    }
    &.isWaiting {
        a {
            background-color: $color-green;
        }
    }
}