// Type parcours
.typeParcours {
    .cheese {
        margin: 45px auto 0 auto;
    }
}
.cheese {
    display: flex;
    width: 280px;
    height: 280px;
    position: relative;
    border-radius: 50%;
    box-shadow: 0 3px 6px rgba(0,0,0,.16);
    &:after {
        content: '';
        position: absolute;
        width: 1px;
        height: 100%;
        background: $color-white;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
    }
}
.cheese__left,
.cheese__right {
    display: block;
    height: 100%;
    width: 50%;
    position: relative;
    text-decoration: none;
    color: $color-white;
    box-sizing: border-box;
}
.cheese__left {
    background: $color-green;
    border-bottom-left-radius: 280px;
    border-top-left-radius: 280px;
    padding: 80px 10px 80px 40px;
    text-align: right;
}
.cheese__right {
    background: $color-blue;
    border-bottom-right-radius: 280px;
    border-top-right-radius: 280px;
    padding: 80px 40px 80px 10px;
    text-align: left;
}
.cheese__title {
    text-transform: uppercase;
    display: block;
    font-size: 3rem;
    font-weight: bold;
    margin: 0 0 10px;
}
.cheese__txt {
    display: block;
    font-size: 1.4rem;
    line-height: 2rem;
}