// Grille positionnement
.grillePosition {
    margin: 0 10px;
    .spotlight,
    .innerTitle {
        margin: 0 0 20px;
    }
}
.grillePosition__list {
    max-height: 290px;
    overflow-x: visible;
    overflow-y: scroll;
    padding: 6px;
    a {
        display: flex;
        align-items: center;
        text-decoration: none;
        color: $color-white;
        height: 48px;
        padding: 0 20px 0 60px;
        position: relative;
    }
    .icone {
        width: 60px;
        height: 60px;
        background-color: $color-white;
        box-sizing: border-box;
        border: 1px solid $color-blue;
        display: block;
        position: absolute;
        left: -5px;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;

    }
    .title {
        flex-grow: 1;
    }
    .complete {
        min-width: 25px;
        height: 48px;
        background: url("../images/ico-check-white.svg") no-repeat center center;
    }
}
.grillePosition__item {
    background: $color-blue;
    border-radius: 24px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16);
    margin: 0 0 24px;
    &:last-child {
        margin-bottom: 0;
    }
}